.App {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.container {
  margin: 5%;
  border: 10px solid rgb(21, 124, 28);
  background-image: linear-gradient(white 25%, green);
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
}

/* Header */
.header {
  margin: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center
}

/* Enter */
.enter {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 100px;
  text-align: center
}

.btn {
  max-width: 50%;
  transition: .5s;
  cursor: pointer;
}

.btn:hover {
  opacity: .7;
}

.btn:active {
  opacity: .3;
}

.enter__rooms {
  text-align: center;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  max-width: 700px;
}

/* Room */
.roomChoice {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  margin: 5px;
  margin-bottom: 50px;
  width: 150px;
  height: 150px;
  border-radius: 10px;
}

.room-btn {
  margin: 50px;
  padding: 10px 30px;
  background-color: rgb(21, 124, 28);
  color: rgb(182, 232, 185);
  font-size: 18px;
  border-radius: 5px;
  text-align: center;
}

.room-btn:hover {
  background-color: transparent;
  color: rgb(10, 19, 10);
  font-weight: bold;
  border: 0;
}

.product-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 50px;
  border: 1px solid black;
}

.product-block {
  display: flex;
}

.remove-btn {
  margin-left: 1%;
  background-color: rgb(21, 124, 28);
  color: rgb(182, 232, 185);
  border-radius: 5px;
}

/* Add Room */

.addroom {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.adroom-btn {
  margin: 50px;
}

.add__inputs {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 10px 10px;
  margin-bottom: 50px;
}

.input {
  display: flex;
  margin: 10px 10px;
}

.input__item {
  font-family: inherit;
  width: 100%;
  border: 0;
  border-bottom: 3px solid rgb(6, 69, 21);
  outline: 0;
  font-size: 1.3rem;
  color: rgb(0, 0, 0);
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s;

}

.color {
  width: 40px;
  height: 40px;
  border: 2px solid rgb(6, 69, 21);
  margin-left: 10px;
  border-radius: 4px;
  background: transparent;
}

.color-input {
  display: flex;
  align-items: center;
  font-size: 22px;
}



/* Modal */
.global {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.429);
  display: flex;
  justify-content: center;
  align-items: center;
}

.cont {
  display: flex;
  justify-content: center;
  height: 50%;
  width: 50%;
  background-color: rgb(229, 251, 230);
  position: relative;
}

.cont button {
  position: absolute;
  background-color: rgb(6, 69, 21);
  color: white;
  border: 1px solid rgb(6, 69, 21);
  border-radius: 4px;
  top: 20px;
  right: 20px;
  width: 35px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cont button:hover {
  cursor: pointer;
  font-size: 18px;
}